/**************************
	* STYLES
**************************/
/* variables */
:root {
  --primary: #003E52;
  --secondary: #EE7623;
  --neutral: #00000029;
  --white: #FFFFFF;
  --gray: #B4B3B3;
  --font-size-base: 16px;
  --font-size-large: 24px;
  --font-size-xlarge: 42px;
  --spacing: 20px;
  --box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.29);
}

/* reset */
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

li {
  list-style-type: none;
}

/* base styles */
body {
  overflow-x: hidden;
  font-family: "Rift Regular";
  width: auto;
}

button {
  background: none;
  border: none;
}

/* fonts */
/* Rift Bold */
@font-face {
  font-family: "Rift Bold";
  src: url("/fonts/Rift/Fort Foundry - Rift-Bold.otf");
}
@font-face {
  font-family: "Rift Light";
  src: url("/fonts/Rift/Fort Foundry - Rift-Light.otf");
}
@font-face {
  font-family: "Rift Medium";
  src: url("/fonts/Rift/Fort Foundry - Rift-Medium.otf");
}
@font-face {
  font-family: "Rift Regular";
  src: url("/fonts/Rift/Fort Foundry - Rift-Regular.otf");
}
/* Montseratt */
@font-face {
  font-family: "Montseratt Bold";
  src: url("/fonts/Montserrat/Montserrat-Bold.ttf");
}
@font-face {
  font-family: "Montseratt Light";
  src: url("/fonts/Montserrat/Montserrat-Light.ttf");
}
@font-face {
  font-family: "Montseratt Medium";
  src: url("/fonts/Montserrat/Montserrat-Medium.ttf");
}
@font-face {
  font-family: "Montseratt Regular";
  src: url("/fonts/Montserrat/Montserrat-Regular.ttf");
}
h1, h2, h3, h4, h5, p, a, li {
  color: var(--primary);
  font-weight: normal;
}

h1, h2, h3, h4, h5 {
  font-size: 24px;
}

/* ===== MOBILE STYLES ===== */
/* navbar styles */
.nav, footer {
  left: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 999;
  background-color: var(--primary);
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
  height: 74px;
}

.nav {
  position: fixed;
  top: 0;
  margin-bottom: 74px;
}

nav div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 16px;
}

.menu-items {
  height: 70%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.menu-items ul {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  gap: 2rem;
  font-size: 24px;
}

.nav ul li a {
  color: var(--white);
}

/* hamburger menu styles */
.hamburger-menu span {
  display: block;
  width: 30px;
  height: 3px;
  background-color: var(--white);
  margin: 5px auto;
  transition: all 0.3s ease;
}

/* .close span:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.close span:nth-child(2) {
    opacity: 0;
}

.close span:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
} */
.close {
  position: relative;
  width: 60px;
  height: 60px;
  background-color: transparent; /* Optional: Set background color */
  cursor: pointer; /* Indicate clickable element */
  transition: all 0.3s ease;
}

.close:before,
.close:after {
  content: ""; /* Empty content for pseudo-elements */
  position: absolute;
  top: 25%;
  left: 50%;
  width: 2px;
  height: 50%;
  background-color: white; /* Color of the X */
  transform-origin: center; /* Rotation point */
}

.close:before {
  transform: rotate(45deg); /* Rotate first line */
}

.close:after {
  transform: rotate(-45deg); /* Rotate second line */
}

/* hero slide section styles */
.hero {
  margin-top: 74px;
  /* height: 330px; */
}

.hero-wrapper {
  position: relative;
  margin-bottom: 47px;
}

section div img {
  width: 100%;
  height: 100%;
}

.slide-item {
  position: relative;
  width: 100%;
  height: 350px;
  overflow: hidden;
}

.slide-item > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.slide-item > div {
  position: relative;
  width: 272px;
  text-align: center;
  top: 50%;
  left: 70%;
  transform: translate(-70%, -50%);
  color: var(--primary);
  font-size: var(--font-size-xlarge);
  font-family: "Montseratt Bold";
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.slide-item div h1 {
  margin-bottom: 1rem;
}

.slick-prev, .slick-next {
  position: absolute;
  bottom: 20px;
  z-index: 99;
}

.slick-prev::before {
  content: "<";
  display: inline-block;
  margin-left: 5px;
}

.slick-prev {
  right: 30px;
}

.slick-next {
  right: 10px;
}

.slick-next::after {
  display: inline-block;
  margin-left: 5px;
}

.slide-buttons {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  gap: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.prev, .next {
  /* background-color: #FFFFFF; */
  border-radius: 100%;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.prev span, .next span {
  font-size: 1.5rem;
  color: var(--white);
  font-weight: bolder;
}

.cta {
  cursor: pointer;
  background-color: #FFFFFF;
  box-shadow: var(--box-shadow);
  padding: 7px 12px;
  font-family: "Rift Medium";
  color: var(--primary);
  font-size: 14px;
  font-weight: bolder;
  width: 115px;
  height: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.cta span {
  font-size: var(--font-size-large);
}

/* welcome styles */
.welcome {
  margin-bottom: 90px;
}

.welcome-items {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.welcome-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.welcome-item div {
  width: 69px;
  height: 72px;
  margin-bottom: 25px;
}

.welcome-item h3 {
  width: 230px;
  height: 61px;
  color: var(--primary);
  font-weight: bolder;
  letter-spacing: 1.92px;
  font-family: "Rift Medium";
}

.welcome h3 {
  text-align: center;
}

.welcome .divider {
  background-color: var(--secondary);
  width: 45px;
  height: 2px;
  margin: 0;
}

.welcome .welcome-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.welcome-header h2 {
  font-family: "Montseratt Bold";
  margin-bottom: 0.8rem;
  font-size: 20px;
}

/* footer styles */
footer {
  background-color: var(--primary);
  width: 100%;
  /* bottom: 0; */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* small tablet styles */
/* large tablets and laptop styles */
@media screen and (min-width: 801px) {
  body {
    font-size: 18px;
  }
}
/* desktop styles */
@media screen and (min-width: 1025px) {
  body {
    font-size: 20px;
  }
}
/* desktop styles */
@media screen and (min-width: 1281px) {
  body {
    font-size: 20px;
  }
  .welcome {
    margin-bottom: 90px;
  }
  .welcome .divider {
    background-color: var(--gray);
  }
  .welcome h3 {
    text-align: left;
  }
  .welcome-header h2 {
    font-size: 28px;
  }
  .welcome-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: space-around;
    padding: 0 139px;
  }
  .welcome-item {
    flex: 20%;
    padding: 20px;
    display: flex;
    align-items: flex-start;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .welcome-item:hover {
    cursor: pointer;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.29);
    border-radius: 5px;
  }
  .welcome-item:hover h3 {
    color: var(--secondary);
    text-decoration: underline;
  }
  .menu-items ul {
    display: flex;
    flex-direction: row;
    font-family: "Montseratt Medium";
    font-size: 18px;
    font-weight: bolder;
    margin-top: 33px;
  }
  ul li a {
    text-transform: capitalize !important;
  }
  #nav {
    background-color: transparent;
    box-shadow: none;
    display: flex;
    justify-content: space-between;
  }
  .nav div {
    padding: 14px 138px;
  }
  .nav div a {
    margin-top: 23px;
  }
  #hamburger-menu {
    display: none;
  }
  .hero {
    margin-top: 0;
  }
  .slide-item {
    height: 704px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .slide-item > div {
    width: 510px;
    height: 110px;
    transform: translate(-18%, -60%);
    text-align: left;
    align-items: flex-start;
  }
  .slide-item > div h1 {
    font-size: 42px;
  }
  .slide-buttons {
    right: 138px;
  }
}